}
</script>

<style scoped>
.title {
  font-size: 32px;
}

.el-row {
  margin-top: 20px;
}

.el-button {
  margin-left: 20px;
}

.btn_large {
  margin-top: 20px;
  font-size: 32px;
}
</style>



